"use client"

import { useMemo } from "react"
import { useGameStore } from "@/lib/stores/gameStore"
import { StablePlayerController } from "./StablePlayerController"
import { KeyboardControls } from "@react-three/drei"

// Keyboard controls configuration
const keyboardMap = [
  { name: "forward", keys: ["ArrowUp", "KeyW"] },
  { name: "backward", keys: ["ArrowDown", "KeyS"] },
  { name: "left", keys: ["ArrowLeft", "KeyA"] },
  { name: "right", keys: ["ArrowRight", "KeyD"] },
  { name: "jump", keys: ["Space"] },
  { name: "run", keys: ["ShiftLeft", "ShiftRight"] },
]

export function PlayerCharacters() {
  const playersMap = useGameStore((state) => state.players)
  const currentPlayerId = useGameStore((state) => state.currentPlayerId)
  const players = useMemo(() => Array.from(playersMap.values()), [playersMap])

  return (
    <KeyboardControls map={keyboardMap}>
      {players.map((player) => (
        <StablePlayerController key={player.id} player={player} isLocalPlayer={player.id === currentPlayerId} />
      ))}
    </KeyboardControls>
  )
}
