"use client"

import { RigidBody } from "@react-three/rapier"
import { useMemo } from "react"

export function DaycareEnvironment() {
  // Create decorative elements with memoization for performance
  const decorativeElements = useMemo(() => {
    const elements = []
    
    // Add decorative wall art and posters
    for (let i = 0; i < 8; i++) {
      const angle = (i / 8) * Math.PI * 2
      const radius = 35
      const x = Math.cos(angle) * radius
      const z = Math.sin(angle) * radius
      
      elements.push(
        <RigidBody key={`wall-art-${i}`} type="fixed" colliders="cuboid">
          <mesh position={[x, 3, z]} rotation={[0, angle + Math.PI, 0]}>
            <planeGeometry args={[2, 1.5]} />
            <meshStandardMaterial color={i % 2 === 0 ? "#FFE5EC" : "#D4F1F4"} roughness={0.5} />
          </mesh>
        </RigidBody>
      )
    }
    
    return elements
  }, [])

  return (
    <>
      {/* Ground/Floor with improved texture */}
      <RigidBody type="fixed" colliders="cuboid" friction={1}>
        <mesh receiveShadow position={[0, -0.5, 0]} rotation={[-Math.PI / 2, 0, 0]}>
          <planeGeometry args={[80, 80]} />
          <meshStandardMaterial color="#FFF5F7" roughness={0.8} metalness={0.1} />
        </mesh>
      </RigidBody>

      {/* Outer walls with windows */}
      <WallSegment position={[0, 2, -40]} rotation={[0, 0, 0]} width={80} height={4} color="#E0BBE4" hasWindows={true} />
      <WallSegment position={[0, 2, 40]} rotation={[0, Math.PI, 0]} width={80} height={4} color="#E0BBE4" hasWindows={true} />
      <WallSegment position={[-40, 2, 0]} rotation={[0, Math.PI / 2, 0]} width={80} height={4} color="#FFDFD3" hasWindows={true} />
      <WallSegment position={[40, 2, 0]} rotation={[0, -Math.PI / 2, 0]} width={80} height={4} color="#FFDFD3" hasWindows={true} />

      {/* Interior walls creating rooms */}
      <WallSegment position={[-10, 2, 15]} rotation={[0, 0, 0]} width={20} height={4} color="#D4F1F4" />
      <WallSegment position={[10, 2, -15]} rotation={[0, Math.PI / 2, 0]} width={15} height={4} color="#FFEAA7" />

      {/* Ceiling (optional - creates more enclosed feel) */}
      <RigidBody type="fixed" colliders="cuboid">
        <mesh position={[0, 6, 0]} rotation={[Math.PI / 2, 0, 0]}>
          <planeGeometry args={[80, 80]} />
          <meshStandardMaterial color="#FFFAF0" side={2} transparent opacity={0.9} />
        </mesh>
      </RigidBody>

      {/* Floor decorations - pastel rugs */}
      <mesh position={[0, 0.01, 0]} rotation={[-Math.PI / 2, 0, 0]}>
        <circleGeometry args={[8, 32]} />
        <meshStandardMaterial color="#FFE5EC" roughness={0.9} />
      </mesh>

      <mesh position={[-15, 0.01, -15]} rotation={[-Math.PI / 2, 0, 0]}>
        <circleGeometry args={[5, 32]} />
        <meshStandardMaterial color="#E0BBE4" roughness={0.9} />
      </mesh>

      <mesh position={[15, 0.01, 15]} rotation={[-Math.PI / 2, 0, 0]}>
        <circleGeometry args={[5, 32]} />
        <meshStandardMaterial color="#D4F1F4" roughness={0.9} />
      </mesh>
    </>
  )
}

interface WallSegmentProps {
  position: [number, number, number]
  rotation: [number, number, number]
  width: number
  height: number
  color: string
  hasWindows?: boolean
}

function WallSegment({ position, rotation, width, height, color, hasWindows = false }: WallSegmentProps) {
  return (
    <RigidBody type="fixed" colliders="cuboid">
      <group position={position} rotation={rotation}>
        {/* Main wall - made thinner to reduce camera interference */}
        <mesh castShadow receiveShadow>
          <boxGeometry args={[width, height, 0.2]} />
          <meshStandardMaterial color={color} roughness={0.7} metalness={0.2} />
        </mesh>
        
        {/* Add windows if enabled */}
        {hasWindows && (
          <>
            {/* Window frames */}
            <mesh position={[0, 0, 0.11]}>
              <planeGeometry args={[width * 0.8, height * 0.6]} />
              <meshStandardMaterial color="#D4F1F4" transparent opacity={0.7} />
            </mesh>
            
            {/* Window decorations */}
            <mesh position={[-width * 0.35, height * 0.2, 0.12]}>
              <boxGeometry args={[0.5, 0.1, 0.03]} />
              <meshStandardMaterial color="#FFE5EC" />
            </mesh>
            <mesh position={[width * 0.35, height * 0.2, 0.12]}>
              <boxGeometry args={[0.5, 0.1, 0.03]} />
              <meshStandardMaterial color="#FFE5EC" />
            </mesh>
          </>
        )}
      </group>
    </RigidBody>
  )
}
